function getCQSSC(){
    let open_time = [
        "00:05:00", "00:10:00", "00:15:00", "00:20:00", "00:25:00", "00:30:00", "00:35:00", "00:40:00", "00:45:00", "00:50:00", "00:55:00", "01:00:00", "01:05:00", "01:10:00", "01:15:00", "01:20:00", "01:25:00", "01:30:00", "01:35:00", "01:40:00", "01:45:00", "01:50:00", "01:55:00", "10:00:00", "10:10:00", "10:20:00", "10:30:00", "10:40:00", "10:50:00", "11:00:00", "11:10:00", "11:20:00", "11:30:00", "11:40:00", "11:50:00", "12:00:00", "12:10:00", "12:20:00", "12:30:00", "12:40:00", "12:50:00", "13:00:00", "13:10:00", "13:20:00", "13:30:00", "13:40:00", "13:50:00", "14:00:00", "14:10:00", "14:20:00", "14:30:00", "14:40:00", "14:50:00", "15:00:00", "15:10:00", "15:20:00", "15:30:00", "15:40:00", "15:50:00", "16:00:00", "16:10:00", "16:20:00", "16:30:00", "16:40:00", "16:50:00", "17:00:00", "17:10:00", "17:20:00", "17:30:00", "17:40:00", "17:50:00", "18:00:00", "18:10:00", "18:20:00", "18:30:00", "18:40:00", "18:50:00", "19:00:00", "19:10:00", "19:20:00", "19:30:00", "19:40:00", "19:50:00", "20:00:00", "20:10:00", "20:20:00", "20:30:00", "20:40:00", "20:50:00", "21:00:00", "21:10:00", "21:20:00", "21:30:00", "21:40:00", "21:50:00", "22:00:00", "22:05:00", "22:10:00", "22:15:00", "22:20:00", "22:25:00", "22:30:00", "22:35:00", "22:40:00", "22:45:00", "22:50:00", "22:55:00", "23:00:00", "23:05:00", "23:10:00", "23:15:00", "23:20:00", "23:25:00", "23:30:00", "23:35:00", "23:40:00", "23:45:00", "23:50:00", "23:55:00", "23:59:59"
    ]
    // 轮训开奖时间，转换时间戳，对比当前应该有的期号。
    let now_time = moment().valueOf() + 20000
    let stop = false
    let day_id = 1
    let open_v = 1
    open_time.forEach((open_time_v, open_time_i) => {
        if(stop)return
        let old_time = moment(open_time_v,"HH:mm:ss").valueOf()
        if(open_time_i == 0){ open_time_i = 120 }
        if(now_time < old_time){
            open_v = open_time_v
            day_id = open_time_i
            stop = true
        }
    })
    if(day_id == 120){
        day_id = (moment().subtract(1, 'days').format('YYYYMMDD'))+(Array(3).join('0') + day_id).slice(-3)
    }else{
        day_id = (moment().format('YYYYMMDD'))+(Array(3).join('0') + day_id).slice(-3)
    }
    // console.log('【重庆时时彩】当前时间小于>>> '+open_v+' <<<应该采集 - 第 '+day_id+' 期')
    open_v = moment(open_v,"HH:mm:ss").format('YYYY/MM/DD HH:mm:ss')
    let info = [day_id,open_v]
    return info
}
