import axios from 'axios'

let host = 'http://127.0.0.1:8000/api'
host = 'http://192.168.31.176:8000/api'

// host = 'http://154.48.238.102:80/api'


//获取用户信息
export const getSetting = () => { return axios.get(`${host}/setting/`) }
// 登录
export const login = params => { return axios.post(`${host}/login/`, params) }
//获取用户信息
export const getUserDetail = () => { return axios.get(`${host}/users/1/`) }
// 注册
export const register = parmas => { return axios.post(`${host}/users/`, parmas) }
// 获取聊天室名字和地址
export const getRoom = () => { return axios.get(`${host}/room/`) }
// 获取 单独的聊天室 刷新时候避免 没数据····（后期做cookie）
export const getRoomKEY = (e) => { return axios.get(`${host}/room/?url=${e}`) }
// 在线会员数据
export const getRoomUSER = (e) => { return axios.get(`${host}/roomusers/?key=${e}`) }



// get_all_url
export const get_pk10_alls = (e) => { return axios.get(`${host}/pk10/open/?format=json&now_day_id=${e}`) }
export const get_pk10_news = () => { return axios.get(`${host}/pk10/news/?format=json`) }






