<template>
    <div class="open-bg">
        <div class="opens">
            <img src="../assets/img/pk10.png" alt="">
            <div class="other">
                <div class="nos" v-if="tops.opens_all != ''">
                    <i v-for="t_value in tops.opens_all" v-bind:class="t_value.opens_color">
                        {{t_value.opens_no}}
                    </i>
                </div>
                <div class="nos opening" v-else>正在获取数据中...</div>
                <div class="times" >
                    当前期号<i>{{ tops.issue }}</i>,距离下期开奖时间:<i>{{ tops.next_M }}</i> 分<i>{{ tops.next_S }}</i> 秒
                </div>
            </div>
        </div>
        <div class="plans"></div>
    </div>
</template>

<script>
import moment from 'moment'
import { get_pk10_news } from '../api/api'
// 
let _opentime = require('../assets/js/opentime.js'); // 获取时间的js
let _config = require('../api/api.js'); // 配置文件
let _code = 'pk10'
// 
export default {
    name: 'toppk10',
    data(){
        return {
            tops: { 'day_id': 1, 'issue': 100000, 'opens_all': '', 'opened': '000', 'next_M': '00', 'next_S': '00'},
        }
    },
    methods:{
        get_Time(){ // 获取下期时间 和 当前时间 进行对比 // get_Time()
            let get_now_time = moment().format('YYYY/MM/DD HH:mm:ss')
            let need_day_id = _opentime.opens(_code)[0]
            let get_next_time = _opentime.opens(_code)[1] // 2018/11/21 13:07:00 - test
            // test
            // get_next_time = '2018/11/23 09:07:00'
            // get_now_time = '2018/11/22 09:06:59'
            // test
            get_now_time = moment(get_now_time, 'YYYY/MM/DD HH:mm:ss').valueOf()
            get_now_time = get_now_time - 30000
            // get_now_time = get_now_time
            get_next_time = moment(get_next_time, 'YYYY/MM/DD HH:mm:ss').valueOf()
            let dao_time = get_next_time - get_now_time // 得到倒计时时间
            return [need_day_id,dao_time,get_now_time]
        },
        update_Tops(){ // 倒计时到的时候清空头部部分信息
            this.tops.opens_all = ''
        },
        get_News(){ // 获取最新数据
            let _this = this
            window.getNews = setInterval(()=>{ 
                get_pk10_news().then((data)=>{
                    if( _this.tops.day_id == 1 ){ // 头部信息为空时！
                        _this.Tops(data.data)
                    }else{ //头部信息被赋值过
                        if(_this.tops.day_id != parseInt(data.data[0].day_id)){
                            _this.Tops(data.data)
                            clearInterval(window.getNews)
                        }else{
                            console.log('没有采集到正确数据！继续采集')
                        }
                    }
                }).catch(function (error) {
                    console.log(error)
                })
            }, _config.get_time) 
        },
        next_Time(){ // 下一期的倒计时！
            let needDayId = this.get_Time()[0] // 需要当前的id
            let daoTime = this.get_Time()[1] // 下一期时间
            let nowTime = this.get_Time()[2] // 现在时间
            window.setNextTime = setInterval(()=>{ // 开始倒计时操作 console.log( '正在运行倒计时' )
                if( daoTime <= 0 ){
                    clearInterval(window.setNextTime)
                    console.log( 'nextTime,如果出现1次以上就可能会出现问题！！！---[严重错误！]' )
                    // 需要去采集
                    this.next_Time() // 循环自身 
                    this.update_Tops() // 清空头部信息  加1期,显示开奖中
                    this.get_News() // 去采集新的数据
                }else{
                    let _HH = parseInt(daoTime/60/60/1000)
                    this.tops['next_M'] = moment(daoTime).format('mm')
                    this.tops['next_S'] = moment(daoTime).format('ss')
                    // console.log( nowTime )
                    let res = moment( nowTime ).format('HH:mm:ss')
                    res = res.split(':')
                    if( _HH > 0 ){ // 如果超过1小时以上累加到分钟上即可
                        this.tops['next_M'] = parseInt(moment(daoTime).format('mm')) + (_HH*60)
                    }
                    daoTime = daoTime - 1000
                }
            },1000)
        },
        Tops(e){ // 封装头部信息
            if(e.length > 0){ // 大于0点是没数据的
                let t_day_id = e[0].day_id
                let t_issue = parseInt(e[0].issue)
                let t_opens_all = []
                let t_opens = e[0].opens.split(',')
                t_opens.forEach(function(v,i){
                    let _v = parseInt(v) // 转换类型
                    t_opens_all[i] = {
                        'opens_no': _v,
                        'opens_color': _config.api_pk10_color[_v]
                    }
                })
                let t_opened = parseInt(t_day_id.slice(8))
                this.tops['day_id'] = parseInt(t_day_id)
                this.tops['issue'] = t_issue
                this.tops['opens_all'] = t_opens_all
                this.tops['opened'] = t_opened
            }
        },
        checkPage(){ // 初始化一次头部
            let _this = this
            let x2 = parseInt(this.get_Time()[0]) 
            get_pk10_news().then((data)=>{
                if( parseInt(data.data[0].day_id) == x2 ){ // 头部信息为空时！
                    _this.Tops(data.data)
                    clearInterval(window.getTops) 
                }else{ // 头部信息被赋值过
                    console.log('没有采集到正确数据！继续采集')
                }
            }).catch(function (error) {
                console.log(error)
            })     
        },
        TintPage(){ // 初始化页面
            this.checkPage()
            this.next_Time() 
        },
    },
    mounted(){// 页面加载完
        this.TintPage()
    },
    beforeDestroy() { 
        clearInterval(window.setNextTime) // 页面切换销毁 倒计时器
        clearInterval(window.getNews) // 页面切换销毁 采集器！
    } 
}
</script>




