<template>
    <div>
        <div class="loading" v-if="load">
            <mt-spinner type="double-bounce" color="#26a2ff" :size="60"></mt-spinner>
        </div>
        <mt-header fixed title="注册">
            <router-link to="/login" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
        </mt-header>
        <div class="main">
            <div class="members">
                <img src="../assets/logo.png" alt="">
                <mt-field label="昵称" placeholder="请输入用户名" state="" v-model="nickname"></mt-field>
                <mt-field label="手机号" placeholder="请输入手机号" state="" type="tel" v-model="mobile"></mt-field>
                <mt-field label="密码" placeholder="请输入密码" state="" type="password" v-model="password"></mt-field>
                <mt-button type="primary" size="large" @click="onSubmit" plain>注册</mt-button>
            </div>
        </div>
    </div>
</template>

<script>
import cookie from '../assets/js/cookie';
import { register } from '../api/api'
import { getUserDetail } from '../api/api'
import { MessageBox } from 'mint-ui'

export default {
    name: 'reg',
    components: {
        
    },
    data(){
        return{
            load:true,
            nickname:'',
            mobile:'',
            password:''
        }
    },
    methods:{
        onSubmit(){
            let _this = this
            _this.load = true
            let k_l = (_this.nickname).length
            let n_l = _this.mobile
            n_l = n_l.toString()
            let p_l = (_this.password).length
            if(k_l > 10 || k_l < 1){ // 昵称长度错误
                _this.load = false
                MessageBox('提示', '昵称长度必须在1到10位～');
            }else{
                if( n_l.length != 11){ // 账户信息
                    _this.load = false
                    MessageBox('提示', '账户必须位手机号～');
                }else{
                    if( p_l > 16 || p_l < 6 ){ // 密码信息
                        _this.load = false
                        MessageBox('提示', '请输入6至16位长度的密码～');
                    }else{ // 基本验证后提交服务器
                        register({ // 提交昵称，账号手机号，密码
                        nick_name:_this.nickname,
                        mobile:_this.mobile,
                        username:_this.mobile,
                        password:_this.password
                        }).then((response)=>{
                            cookie.setCookie('username',_this.username,1)
                            cookie.setCookie('nickname',response.data.nick_name,1)
                            cookie.setCookie('token',response.data.token,1)
                            _this.$store.dispatch('setInfo') // 更新store数据
                            getUserDetail().then((datas) =>{
                                // console.log(datas.data)
                                cookie.setCookie('id',datas.data.id,1)
                                cookie.setCookie('avatar',datas.data.avatar,1)
                                _this.$store.dispatch('setInfo'); //跳转到首页页面
                                _this.load = false
                                _this.$router.push({ name: 'home'})
                            }).catch(function(err){ // console.log(err)
                                _this.load = false
                                _this.$router.push({ name: 'login'})
                            })
                        }).catch(function (error) {
                            if( 'username' in error ){
                                _this.load = false
                                MessageBox('提示', '用户已经存在');
                            }else if( 'nick_name' in error){
                                _this.load = false
                                MessageBox('提示', '昵称已经存在');
                            }else{
                                _this.load = false
                                MessageBox('提示', '请输入正确的账号信息！');
                            }
                        })

                    }
                }
            }
        },
    },
    mounted(){
        this.load = false
    },
    created(){
        //清除缓存
        cookie.delCookie('token')
        cookie.delCookie('username')
        cookie.delCookie('id')
        cookie.delCookie('nickname')
        cookie.delCookie('avatar')
        //更新store数据
        this.$store.dispatch('setInfo')
    },
    watch:{
        selected:function(){
            let _this = this
            _this.$router.push({
                name:_this.selected
            })
        }
    }
}
</script>
