<template>
    <div>
        <div class="loading" v-if="load">
            <mt-spinner type="double-bounce" :size="60" color="#26a2ff"></mt-spinner>
        </div>
        <!--  -->
        <mt-header fixed title="首页"></mt-header>
        <div class="main">
            <!--  -->

            <!--  -->
            <div class="rooms">
                <div class="rooms-info" v-for="item in rooms" @click="goCHAT(item.url)">
                    <img :src="item.avatar" alt="">
                    <div class="rooms-name">{{item.name}}</div>
                </div>
            </div>
        </div>
        <mt-tabbar v-model="selected" >
            <mt-tab-item id="home">
                <i class="fa fa-home fa-fw"></i><br />聊天室
            </mt-tab-item>

            <mt-tab-item id="member" v-if="userInfo.nickname">
                <i class="fa fa-user fa-fw"></i><br />我的
            </mt-tab-item>
            <mt-tab-item id="login" v-else>
                <i class="fa fa-user fa-fw"></i><br />我的
            </mt-tab-item>
            <mt-tab-item id="member1">
                <i class="fa fa-html5"></i><br />APP
            </mt-tab-item>
        </mt-tabbar>
    </div>
</template>

<script>
import { mapGetters } from 'vuex';
import cookie from '../assets/js/cookie';
import { getRoom } from '../api/api';
import { MessageBox } from 'mint-ui'

export default {
    name: 'home',
    components: {
        
    },
    computed:{
        ...mapGetters({
            userInfo:'userInfo'
        })
    },
    data(){
        return{
            load:true,
            selected:'home',
            rooms:[]
        }
    },
    methods:{
        getRooms(){
            let _this = this
            getRoom().then((response)=>{
                _this.rooms = response.data
                this.load = false
            }).catch((error) => {
                MessageBox('提示', '网络异常，请联系客服人员～');
            })
        },
        goCHAT(url){
            if( this.userInfo.nickname ){
                this.$router.push({
                    name:'chat',
                    path:'/chat/'+url,
                    params:{
                        url:url
                    }
                })
            }else{
                MessageBox('提示', '请先登录!');
            }
        }
    },
    mounted(){ // 请求房间列表信息即可
       this.getRooms()
    },
    watch:{
        selected:function(){
            let _this = this
            _this.$router.push({ name:_this.selected})
        }
    }
}
</script>