<template>
    <div>
        <div class="loading" v-if="load">
            <mt-spinner type="double-bounce" :size="60" color="#26a2ff"></mt-spinner>
        </div>

        <mt-header fixed title="会员中心">
            <mt-button @click="LOGINOUT" slot="right">退出</mt-button>
        </mt-header>


        <div class="main">
            <div class="members">
                <img :src="us.avatar" alt="">
                <div class="members-name">PK开奖网会员<br /><br />
                    <i>{{us.nickname}}</i>
                </div>
                <div class="members-bottom">
                    PK开奖网 - wwww.pk5866.com
                </div>
            </div>
        </div>
        <mt-tabbar v-model="selected" >
            <mt-tab-item id="home">
                <i class="fa fa-home fa-fw"></i><br />聊天室
            </mt-tab-item>
            <mt-tab-item id="member">
                <i class="fa fa-user fa-fw"></i><br />我的
            </mt-tab-item>
            <mt-tab-item id="member1">
                <i class="fa fa-html5"></i><br />APP
            </mt-tab-item>
        </mt-tabbar>
    </div>
</template>

<script>
import cookie from '../assets/js/cookie';
import { login } from '../api/api'
import { getUserDetail } from '../api/api'
import { MessageBox } from 'mint-ui'

export default {
    name: 'member',
    components: {
        
    },
    data(){
        return{
            load:true,
            selected:'member',
            us:{ // 自己的信息
                id:'',
                username:'',
                nickname:'',
                avatar:'',
                token:'',
            },
        }
    },
    methods:{
        LOGINOUT(){
            this.$router.push({name: 'login'})
        },
        getMembers(){
            let _this = this
            _this.us['id'] = cookie.getCookie('id')
            _this.us['username'] = cookie.getCookie('username')
            _this.us['nickname'] = unescape(cookie.getCookie('nickname'))
            _this.us['avatar'] = unescape(cookie.getCookie('avatar'))
            if(cookie.getCookie('token')){ // console.log('有token')
                _this.us['token'] = cookie.getCookie('token')
                this.load = false
            }else{
                _this.$router.push({ name: 'home'})
            }
        },
    },
    mounted(){ // 从cookie中获取个人信息
        this.getMembers()
    },
    watch:{
        selected:function(){
            let _this = this
            _this.$router.push({
                name:_this.selected
            })
        }
    }
}
</script>

