<template>
    <div>
        <div class="loading" v-if="load">
            <mt-spinner type="double-bounce" :size="60" color="#26a2ff"></mt-spinner>
        </div>
        <mt-header fixed title="登录">
            <mt-button @click="REG" slot="right">注册</mt-button>
        </mt-header>
        <div class="main">
            <div class="members">
                <img src="../assets/logo.png" alt="">
                <mt-field label="手机号" placeholder="请输入手机号" state="" type="tel" v-model="mobile"></mt-field>
                <mt-field label="密码" placeholder="请输入密码" state="" type="password" v-model="password"></mt-field>
                <mt-button type="primary" size="large" @click="onSubmit" plain>登录</mt-button>
            </div>
        </div>
        <mt-tabbar v-model="selected" >
            <mt-tab-item id="home">
                <i class="fa fa-home fa-fw"></i><br />聊天室
            </mt-tab-item>
            <mt-tab-item id="login">
                <i class="fa fa-user fa-fw"></i><br />我的
            </mt-tab-item>
            <mt-tab-item id="member1">
                <i class="fa fa-html5"></i><br />APP
            </mt-tab-item>
        </mt-tabbar>
    </div>
</template>

<script>
import cookie from '../assets/js/cookie';
import { login } from '../api/api'
import { getUserDetail } from '../api/api'
import { MessageBox } from 'mint-ui'

export default {
    name: 'login',
    components: {
        
    },
    data(){
        return{
            load:true,
            selected:'login',
            mobile: '',
            password:''
        }
    },
    methods:{
        REG(){
            this.$router.push({name: 'reg'})
        },
        onSubmit(){
            let _this = this
            _this.load = true
            let _c = _this.mobile
            _c = _c.toString()
            // console.log( )
            if( _c.length == 11 && (_this.password).length != 0){
                login({
                    username:_this.mobile,
                    password:_this.password
                }).then((response)=>{
                    cookie.setCookie('username',_this.mobile,1)
                    cookie.setCookie('token',response.data.token,1)
                    _this.$store.dispatch('setInfo')
                    getUserDetail().then((datas) =>{
                        cookie.setCookie('id',datas.data.id,1)
                        cookie.setCookie('nickname',datas.data.nick_name,1)
                        cookie.setCookie('avatar',datas.data.avatar,1)
                        _this.$store.dispatch('setInfo'); //跳转到首页页面
                        _this.load = false
                        _this.$router.push({ name: 'home'})
                    }).catch(function(err){
                        cookie.delCookie('token')
                        cookie.delCookie('username')
                        cookie.delCookie('id')
                        cookie.delCookie('nickname')
                        cookie.delCookie('avatar')
                        //更新store数据
                        _this.$store.dispatch('setInfo')
                        _this.load = false
                        MessageBox('提示', '网络异常，请重新登录～');
                    })
                }).catch(function (error) { // console.log(error)
                    _this.load = false
                    MessageBox('提示', '请输入正确的账号信息！');
                })
            }else{
                _this.load = false
                MessageBox('提示', '请输入正确的账号信息！');
            }
        },
    },
    mounted(){
        this.load = false
    },
    created(){
        //清除缓存
        cookie.delCookie('token')
        cookie.delCookie('username')
        cookie.delCookie('id')
        cookie.delCookie('nickname')
        cookie.delCookie('avatar')
        //更新store数据
        this.$store.dispatch('setInfo')
    },
    watch:{
        selected:function(){
            let _this = this
            _this.$router.push({
                name:_this.selected
            })
        }
    }
}
</script>


