<template>
    <div style="height:100%;overflow: hidden;">
        <div class="loading" v-if="load">
            <mt-spinner type="double-bounce" :size="60" color="#26a2ff"></mt-spinner>
        </div>
        <!--  -->
        <mt-header fixed title="聊天室">
            <router-link to="/" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
        </mt-header>
        <div class="main">
            <div class="chats">
                <div class="open">
                    <OpenPk10 />
                </div>
                <div class="msg-bg" v-bind:style="{height:fullHeight+'px'}">
                    <div class="msg" id="MSG">
                        <div class="msgs" v-for="item in MSGDATA">
                            <div v-if="item.type === 'join'">
                                <div v-bind:class="item.type">{{item.nickname}} 加入聊天室</div>
                            </div> 
                            <div v-else-if="item.type === 'out'">
                                <div v-bind:class="item.type">{{item.nickname}} 离开聊天室</div>
                            </div> 
                            <div v-else-if="item.type === 'other'">
                                <div v-bind:class="item.type">
                                    <div class="msgs-name">{{item.nickname}}</div>
                                    <div class="msgs-main">
                                        <div class="msgs-pic"><img :src="item.avatar" alt=""></div>
                                        <div class="msgs-message">{{item.message}}</div>
                                    </div>
                                </div>
                            </div>  
                            <div v-else-if="item.type === 'us'">
                                <div v-bind:class="item.type">
                                    <div class="msgs-main">
                                        <div class="msgs-message">{{item.message}}</div>
                                    </div>
                                </div>
                            </div>   
                        </div>        
                    </div>
                </div>
                <div class="push-bg">
                    <div class="push">
                        <div class="txt">
                            <input type="text" v-model="sendMSG" maxlength="100" @keyup.enter.native="SEND" placeholder="请输入发送消息...">
                        </div>
                        <!--  -->
                        <!-- <div class="send" v-if="sended">发送中</div> -->
                        <div class="send" @click="SEND()" >发送</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import OpenPk10 from '../components/OpenPk10.vue'
import cookie from '../assets/js/cookie';
import { MessageBox } from 'mint-ui'
import { getRoomKEY } from '../api/api'
import { getRoomUSER } from '../api/api'
// 
let wsHOST = 'ws://127.0.0.1:8000/ws'
wsHOST = 'ws://192.168.31.176:8000/ws'
// let wsHOST = 'ws://154.48.238.102:80/ws'
let chatSocket
// 
export default {
    name: 'chat',
    components: {
        OpenPk10,
    },
    data(){
        return{
            load:true,
            sended:false,
            fullHeight: document.documentElement.clientHeight - 165,
            picurl:'',
            room:{ // 房间信息
                roomname:'',
                roomkey:'',
                roomavatar:''
            },
            us:{ // 自己的信息
                id:'',
                username:'',
                nickname:'',
                avatar:'',
                token:'',
            },
            tt:[], // 在线的人的信
            sendMSG:'',
            MSGDATA:[]
        }
    },
    methods:{
        initHeight(){
            const _this = this
            window.onresize = () => {
                return (() => {
                    window.fullHeight = document.documentElement.clientHeight
                    _this.fullHeight = window.fullHeight
                })()
            }
            _this.load = false
        },
        SEND(){
            console.log('发送信息')
            let _this = this
            if(_this.sendMSG != ''){
                let message = {
                    id:_this.us['id'],
                    message:_this.sendMSG
                }
                _this.sendMSG = ''
                chatSocket.send(JSON.stringify({ // 发送消息
                    'message': message
                }));
            }
        },
        checkCookie(){ // 判断是否有token
            let _this = this
            _this.us['id'] = cookie.getCookie('id')
            _this.us['username'] = cookie.getCookie('username')
            _this.us['nickname'] = unescape(cookie.getCookie('nickname'))
            _this.us['avatar'] = unescape(cookie.getCookie('avatar'))
            if(cookie.getCookie('token')){ // console.log('有token')
                let pic = unescape(cookie.getCookie('avatar'))
                _this.picurl = (pic.split('media'))[0]+'media/'
                _this.us['token'] = cookie.getCookie('token')
                _this.room['roomkey'] = `${_this.$route.params.url}`
                _this.getRooms(_this.room['roomkey'])
            }else{ // 没有token跳转首页
                _this.$router.push({ name: 'home'})
            }
        },
        getRooms(e){ // 刷新了 重新获取房间信息 - 但要有token认证
            let _this = this
            getRoomKEY(e).then((response)=>{
                _this.room['roomname'] = response.data[0].name
                _this.room['roomavatar'] = response.data[0].avatar
                _this.ws()
            }).catch(function (error) {
                console.log(error)
            })
        },
        ws(key){ // ws连接
            let _this = this
            let url = wsHOST+'/chat/'+_this.room['roomkey']+'/?token='+_this.us['token']
            //
            chatSocket = new WebSocket(url);
            //
            this.getTTUsers()
            // 当接收到信息
            chatSocket.onmessage = function(e) { // console.log(e)
                let info = (JSON.parse(e.data)).message // console.log( info )
                if(info['type'] == 'join'){ // 获取到join的数据 加入tt内
                    // console.log( '获取到join的数据 加入tt内' )
                    info['avatar'] = _this.picurl+info['avatar']
                    _this.tt.unshift(info)
                }else if(info['type'] == 'out'){
                     _this.tt.forEach((v,i) => { // 遍历查询到就赋值
                        if( v['id'] == info['id'] ){
                            _this.tt.splice(i,1)
                        }
                    })
                }else if(info['type'] == 'other'){
                    console.log('获取到消息了')
                    if(info['id'] ==  _this.us['id']){ // 如果消息是自己发的
                        info['type'] = 'us'
                    }else{ //需要去 tt里面获取信息
                        let _avatar
                        let _nickname
                        _this.tt.forEach((v,i) => { // 遍历查询到就赋值
                            if( v['id'] == info['id'] ){
                                _avatar = v['avatar']
                                _nickname = v['nickname']
                            }
                        });
                        info = {
                            type:info['type'],
                            message:info['message'],
                            avatar:_avatar,
                            nickname:_nickname
                        }
                    }
                }
                _this.MSGDATA.push(info) // 按类型发消息
            };
            // 当服务器关闭连接后，跳转首页
            chatSocket.onclose = function(e) {
                console.log('Chat 关闭了链接,发消息给所有人')
                // _this.$router.push({ name: 'home'})
            };
            //
        },
        getTTUsers(){
            console.log('请求服务器在线会员数据数据～')
            let _this = this
            getRoomUSER(_this.room['roomkey']).then((response)=>{ // console.log(response)
                response.data.forEach(element => {
                    console.log( element['_id'] )
                    if( element['_id'] == _this.us['id']){ // 获取到自己的数据不添加  不然的话 加入消息那边会出错
                    }else{
                        let xx =  { 
                            "type": "join", 
                            "nickname": element['nickname'], 
                            "avatar": _this.picurl+element['avatar'],
                            "id": element['_id']
                        }
                        _this.tt.push(xx)
                    }
                });
            }).catch(function (error) {
                console.log(error)
            })
        },



    },
    mounted(){ // 
        this.initHeight()
        this.checkCookie()
        
        console.log(this.room['roomkey'])

    },
    beforeDestroy(){
        console.log('正常切换页面时候，主动关闭socket连接...')
        chatSocket.close();
    },
    watch: {
        MSGDATA(){ // 获取到新的信息 计算高度，将起下拉到最底部，不延迟，计算不了自身的高度
            setTimeout(function(){
                document.getElementById('MSG').scrollTop = document.getElementById('MSG').scrollHeight
            },1)
        },
        fullHeight (val) {
            if(!this.timer) {
            this.fullHeight = val
            this.timer = true
            let _this = this
            setTimeout(function (){
                _this.timer = false
            },333)
            }
        }
    }
}
</script>


