    box-shadow: 2px 2px 12px #999;
    -webkit-box-shadow:2px 2px 12px #999;
    margin: 3px 6px 8px;
}
.us .msgs-message:before{
    content: '';
    position: absolute;
    top: 20px;
    left: initial;
    right: -7px;
    width:0;
    height:0;
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
    border-left: 9px solid #2babff;
    border-right:initial;
}
.join,
.out{
    text-align: center;
    font-size: 13px;
